.sliders-topic {
	margin-bottom: 64px;
	@media (max-width: $md2+px){
		margin-bottom: 35px;
	}
	@media (max-width: $md3+px){
		margin-bottom: 24px;
	}
	@media (max-width: $md3+px){
		margin-bottom: 15px;
	}

	// .sliders-topic__content
	&__content {
		padding: 0;
	}

	// .sliders-topic__item
	&__item {
		background-color: #fff;
		border-radius: 4px;
		box-shadow: 4px 4px 20px 0 rgb(0 0 0 / 10%);
		width: 100%;
	}

	// .sliders-topic__wrapper
	&__wrapper {
	}

	// .sliders-rubric__title
	&__title {
		text-align: center;
		margin: 0 76px;
		@media (max-width: $md2+px){
			margin: 0 35px;
		}
		@media (max-width: $md3+px){
			margin: 0 20px;
		}
	}

	// .sliders-topic__rubric-name
	&__topic-name {
		transition: all 0.3s ease 0s;
		color: $dark;

		@media (min-width: $md2+px) {
			&:hover {
				color: $accent;
			}
		}
	}

	// .sliders-topic__slider
	&__slider {
		margin: 16px 0;
		position: relative;
	}

	// .sliders-rubric__button-wrap
	&__button-wrap {
		position: absolute;
		top: 27%;
		transform: translateY(-50%);
		width: 42px;
		height: 42px;
		border-radius: 50%;
		background: $white;
		box-shadow: 0 2px 6px 0 rgb(0 0 0 / 10%);
		opacity: 0.94;
		cursor: pointer;
		display: flex;
		align-items: center;
		justify-content: center;

		@media (min-width: $md2+px) {
			&:hover {
				.swiper-button_prev-icon,
				.swiper-button_next-icon {
					transition: all 0.2s ease 0s;
					color: lighten($dark, 15%);
				}
			}
		}
		@media (max-width: 559px) {
			display: none;
		}
	}

	// .sliders-topic__button
	&__button {
		position: relative;
		width: 13px;
		height: 13px;
	}
}

.slider {
	// .slider_home-page
	&_home-page {
		padding: 0px 76px;		
		@media (max-width: $md2+px){
			padding: 0px 60px;
		}
		@media (max-width: $md3+px){
			padding: 0px 35px;
		}
		@media (max-width: 559px){
			padding: 0px 20px;
		}
		@media (min-width:559px){
			-webkit-mask-image: linear-gradient(90deg, transparent, #292929 6%, #292929 94%, transparent);
		}
	}

	// .slider__slide
	&__slide {
		margin-bottom: 24px;
	}
}

.swiper-button {
	// .swiper-button_prev
	&_prev {
		left: 40px;
	}

	// .swiper-button_prev-icon
	&_prev-icon {
		transform: rotate(45deg);
	}

	// .swiper-button_next
	&_next {
		right: 40px;
	}

	// .swiper-button_next-icon
	&_next-icon {
		transform: rotate(225deg);
	}

	// .swiper-button__wrap
	&__wrap {
	}
}

.icon-arrow-slider {
	color: $black;
}
