.grid {

	// .grid__content
	&__content {	
	}

	// .grid__wrap
	&__wrap {
		align-items: start;
		display: grid;
		grid-auto-flow: dense;
		grid-template-columns: repeat(3, 300px);
		justify-content: space-between;
		@media (max-width: 999px) {
			grid-template-columns: repeat(2, 45%);
		}
		@media (max-width: 559px) {
			display: block;
		}
	}

	// .grid__item
	&__item {
		margin-bottom: 32px;
		position: relative;

		// .grid__item_big
		&_big {
			@media (min-width: 559px) {
				grid-column-end: span 2;

				.post__img {
					position: relative;
					margin-bottom: 0;
				}
				.post__info {
					bottom: 0;
					left: 0;
					padding: 10px 30px 30px;
					position: absolute;
					right: 0;
				}
				.post__title {
					color: $white;
				}
			}
		}
	}

	// .grid__banner
	&__banner {
	}
}

.post {
	margin-bottom: 32px;

	// .post__img
	&__img {
		margin-bottom: 8px;
		padding-bottom: 66.6666666667%;
		display: block;
	}

	// .post__img-link
	&__img-link {
		width: 100%;
		height: 100%;
	}

	// .post__info
	&__info {
	}

	// .post__info-top
	&__info-top {
		margin-bottom: 8px;
	}

	// .post__tag
	&__tag {
		color: $accent;
		font-weight: 700;
		display: inline;
	}

	// .post__date
	&__date {
		display: inline;
		color: $dark-light;
	}

	// .post__title
	&__title {
		display: inline-block;
		color: $dark;
	}
}