// Font
$font-family-base: "Open Sans", sans-serif;
$font-family-second: "Georgia";
$font-family-third: "Roboto Condensed", sans-serif;
$font-family-four: "Sinews Sans Pro";
$font-family-five: "Georgia-Bold";
$enable-smooth-scroll: false;
// 62.5% = 10px
$fontSize: 62.5%;

// Границы адаптива ======================
$minWidth: 320px; // Указываем минимальный размер экрана
$maxWidth: 1920; // Указываем размер полотна
$maxWidthContainer: 1100; // Указываем размер ограничивающего контейнера

$md1: $maxWidthContainer + 12;
$md2: 991.98;
$md3: 767.98;
$md4: 479.98;

//<ОСНОВНАЯ СЕТКА>================================================================
._container {
	max-width: $maxWidthContainer + px;
	margin: 0 auto;
	padding: 0 32px;
	@media (max-width: $md4+px) {
		padding: 0 16px;
	}
}
//</ОСНОВНАЯ СЕТКА>=================================================================

//</СТАТЬИ СЕТКА>=================================================================
._container-post {
	max-width: 544px;
	margin: 0 auto;
	padding: 24px 32px;
	@media (max-width: $md1+px) {
	}
	@media (max-width: 600px) {
		padding: 24px 16px;
	}
	@media (max-width: $md4+px){
		padding: 18px 14px;
	}
}
//</СТАТЬИ СЕТКА>=================================================================
//</ЛЕНТЫ НОВОСТЕЙ СЕТКА>=================================================================
._container-lenta {
	max-width: 668px;
	margin: 0 auto;
	padding: 0 16px;
}
//</ЛЕНТЫ НОВОСТЕЙ СЕТКА>=================================================================

// Переменные
$border-radius: 1rem;
$br: $border-radius;
/*
 * Color system
 * ---------------------------------------------
 */

$white: #fff;
$black:    #000;

// scss-docs-end colors-map

$primary:       #292929;
$primary-light: #EF8FA1;
$secondary:     #F4B1BD;
$success:       green;
$info:          #666565;
$warning:       yellow;
$danger:        red;
$accent-light:  #8bc8e4;
$accent:        #3CA0D0;
$dark:          #292929;
$dark-light:    #8a99a6;
$dark-lighter:  #f7f7fa;
$light:			#9b9b9b;

