/* Titles
 ========================================================================= */
h1, .h1,
h2, .h2,
h3, .h3,
h4, .h4,
h5, .h5,
h6 .h6
        line-height: 1.4

/**
 * Responsive titles
 */
h1, .h1
    font-family: $font-family-base
    font-weight: 800
    font-style: normal
    font-size: 2.4rem

    @media (max-width: $md4+px)
            font-size: 2.2rem

h2, .h2
    font-family: $font-family-base
    font-weight: 700
    font-style: normal
    font-size: 2.4rem
    text-decoration-style: solid


h3, .h3
    font-family: $font-family-base
    font-style: normal
    font-weight: 700
    font-size: 1.8rem
    

h4, .h4
    font-family: $font-family-base
    font-style: normal
    font-weight: normal
    font-size: 1.2rem
    text-decoration-style: solid


h5, .h5
    font-family: $font-family-base
    font-style: normal
    font-weight: normal
    font-size: 1.1rem
    text-decoration-style: solid
    text-transform: uppercase
    letter-spacing: 1px


h6, .h6
    font-family: $font-family-third
    font-style: normal
    font-size: 1.8rem
    font-weight: 400
    line-height: 1.4

		

