/* Include fonts
   ========================================================================= */

/**
 * Open Sans
 */
@import url(https://fonts.googleapis.com/css?family=Open+Sans:regular,italic,700,800&display=swap)

// Roboto Condensed
@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed:regular,italic,700,700italic&display=swap)

@include fontFace('Sinews Sans Pro', 'SinewsSansPro-Medium', 600, 'normal', 'woff2')
@include fontFace('Georgia', 'Georgia', 400, 'normal', 'woff2')
@include fontFace('Georgia-Bold', 'Georgia-Bold', 700, 'normal', 'woff2')