.slider-post {
	// .slider-post__control-block
	&__control-block {
		margin: 20px auto;
		display: flex;
		justify-content: center;
		align-items: center;
	}

	// .slider-post__button
	&__button {
		cursor: pointer;
		display: flex;
		align-items: center;
		justify-content: center;
		width: 42px;
		height: 42px;
		border-radius: 50%;
		background: $white;
		box-shadow: 0 2px 6px 0 rgb(0 0 0 / 10%);

		@media (min-width: $md2+px) {
			&:hover {
				.slider-post__button_left-icon,
				.slider-post__button_right-icon {
					color: lighten($dark, 30%);
				}
			}
		}

		// .slider-post__button_left
		&_left {
		}

		// .slider-post__button_left-icon
		&_left-icon {
		}

		// .slider-post__button_right
		&_right {
		}

		// .slider-post__button_right-icon
		&_right-icon {
		}
	}

	// .slider-post__button-wrap
	&__button-wrap {
		position: relative;
		width: 13px;
		height: 13px;
	}

	// .slider-post__fraction
	&__fraction {
		font-family: $font-family-base;
		color: $light;
		margin: 0 16px;
		font-size: 1.6rem;
	}

	// .slider-post__image-wrap
	&__image-wrap {
		padding-bottom: 100%;
		display: block;
		position: relative;
	}

	// .slider-post__image
	&__image {
	}
}
