.icon-menu {
	display: none;
	@media (max-width: 835px) {
		transition: all 0.3s ease 0s;
		display: block;
		position: absolute;
		top: 18px;
		left: 48px;
		width: 25px;
		height: 18px;
		cursor: pointer;
		z-index: 5;
		@media (max-width: $md3+px){
			left: 35px;
		}
		@media (max-width: $md4+px){
			left: 20px;			
		}
		span {
			transition: all 0.3s ease 0s;
			top: calc(50% - 1px);
			left: 0;
			position: absolute;
			width: 100%;
			height: 2px;
			background-color: $white;
			&:first-child {
				top: 0;
			}
			&:last-child {
				top: auto;
				bottom: 0;
			}
		}
		&._active {
			top: 36px;
			position: fixed;
			left: calc(100% - 55px);
			span {
				transform: scale(0);
				&:first-child {
					transform: rotate(-45deg);
					top: calc(50% - 1px);
				}
				&:last-child {
					transform: rotate(45deg);
					bottom: calc(50% - 1px);
				}
			}
			@media (max-width: $md3+px){
				left: calc(100% - 55px);			
			}
		}
	}
}

// .menu__body
.menu__body {
	display: none;
	@media (max-width: 835px) {
		display: flex;
		flex-direction: column;
		justify-content: flex-start;

		transform: translate(-100%, 0);
		transition: 0.3s ease;
		position: fixed;
		z-index: 3;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		// ? Позволяет списку выпадающего меню прокручиваться/ ставиться по необходимости
		overflow: auto;
		padding: 30px 30px 30px 30px;
		background-color: $dark;
		&._active {
			transform: translate(0px, 0%);
		}
	}
}

// menu__list
.menu__list{
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	li{
		margin: 13px 0;
	}

	// menu__list_top
	&_top{
		li{
			&:first-child{
				margin-bottom: 17px;
				margin-top: 0;
			}
			&:last-child{
				margin-bottom: 25px;
			}
		}
	}

	// menu__list_bottom
	&_bottom{
		border-top: 1px solid $accent;
		li{
			&:first-child{
				margin-top: 25px;
			}
			&:last-child{
				margin-bottom: 32px;
			}
		}
	}	
}
