.go-top
    align-items: center
    background-color: $accent
    bottom: 130px
    color: $white
    cursor: pointer
    display: none
    font-size: 24px
    position: fixed
    right: 15px
    text-decoration: none
    transition: background-color .2s ease
    opacity: .5
    justify-content: center
    height: 50px
    width: 40px
    z-index: 20
    @media (max-width: $md2+px)
        right: 30px
    @media (max-width: $md4+px)
        right: 18px
        bottom: 115px

    &.is-show
        display: flex

        &:hover
            transition: background-color .2s ease
            opacity: 1

    .icon
        fill: $white
