.header {
	// .header__top
	&__top {
		background-color: $dark;
		&._active {
			position: relative;
		}
	}

	// .header__bottom
	&__bottom {
		box-shadow: 0 0 6px 0 rgb(0 0 0 / 10%);
		@media (max-width: 835px) {
			display: none;
		}
	}

	// .header__content
	&__content {
		margin: 0 auto;
		padding: 0 48px;

		@media (max-width: $md3+px) {
			padding: 0 32px;
		}
		@media (max-width: $md4+px) {
			padding: 0 16px;
		}
	}

	// .header__menu
	&__menu {
		padding: 10px 0;
	}
}
.menu {
	// .menu__row
	&__row {
		display: flex;
		justify-content: space-between;
		align-items: center;
	}

	// .menu__row-bottom
	&__row-bottom {
		padding: 10px 0;
	}

	// .menu__column
	&__column {
		display: flex;

		// .menu__column_left
		&_left {
			justify-content: flex-start;
			align-items: center;
		}

		// .menu__column_right
		&_right {
			justify-content: flex-end;
			align-items: center;
		}
	}

	// .menu__logo
	&__logo {
		margin-top: -4px;
		margin-right: 30px;

		@media (max-width: $md2+px) {
			font-size: 3rem;
		}
		@media (max-width: 835px) {
			margin-left: 50px;
		}
		@media (max-width: $md3+px) {
			font-size: 2.6rem;
		}
		@media (max-width: $md4+px) {
			font-size: 2.2rem;
		}
	}

	// .menu__rubric
	&__rubric {
		color: $white;
		margin-right: 20px;
		transition: all 0.2s ease 0s;
		@media (min-width: $md2+px) {
			&:hover {
				color: $accent;
			}
		}
	}

	// .menu__topic
	&__topic {
		color: $dark;
		text-transform: none;
		font-size: 1.2rem;
		line-height: 1.6;
		margin-right: 20px;
		transition: all 0.2s ease 0s;
		@media (min-width: $md2+px) {
			&:hover {
				color: $accent;
			}
		}
		@media (max-width: 835px) {
			color: $white;
			font-size: 1.3rem;
		}
	}

	// .menu__button
	&__button {
		margin-right: 30px;
		letter-spacing: 3px;
		@media (max-width: $md3+px) {
			margin-right: 20px;
			letter-spacing: 2px;
		}
		@media (max-width: 435px) {
			margin-right: 0;
			font-size: 1.3rem;
		}
	}

	// .menu__button-wrapper
	&__button-wrapper {
		width: 22px;
		height: 22px;
		position: relative;
		stroke-width: 2;
	}

	// .menu__search
	&__search {
		// .menu__search_lens
		&_lens {
			.menu__search_icon-lens {
				stroke: $white;
				stroke-width: 3;
				@media (min-width: $md2+px) {
					&:hover {
						stroke: $accent;
					}
				}
			}
		}

		// .menu__search_icon-lens
		&_icon-lens {
			color: $white;
			@media (min-width: $md2+px) {
				&:hover {
					transition: all 0.2s ease 0s;
					color: $accent;
				}
			}
		}
		&._active {
			display: none;
		}
	}
}

.logo {
	font-family: $font-family-four;
	font-weight: 700;
	font-style: normal;
	font-size: 3.2rem;
	text-decoration-style: solid;
	color: $white;
	line-height: 1;
	@media (min-width: $md2+px) {
		&:hover {
			transition: all 0.2s ease 0s;
			color: $accent;
		}
	}
}

.icon-search {
}
.icon-close {
	width: 16px;
	height: 16px;
	top: 2px;
	left: 2px;
}
