.news-block {
	margin-bottom: 30px;

	// .news-block__wrap
	&__wrap {
	}

	// .news-block__content
	&__content {
			padding: 15px 0 5px 0;
			border-bottom: 1px solid $accent;
			
			@media (max-width: $md3+px){
				padding: 10px 0 5px 0;
			}
	}

	// .news-block__title
	&__title {
		margin-bottom: 20px;
	}

	// .news-block__title-text
	&__title-text {
	}

	// .news-block__title-icon-wrap
	&__title-icon-wrap {
		display: inline-block;
		position: relative;
		width: 32px;
		height: 12px;
	}

	// .news-block__title-icon
	&__title-icon {
		width: 100%;
		height: 100%;
	}

	// .news-block__title-icon-image
	&__title-icon-image {
		transform: rotate(90deg) scale(3) translate(-0.5px, -3px);
	}

	// .news-block__article-wrap
	&__article-wrap {
		overflow-x: auto;
	}

	// .news-block__row
	&__row {
		overflow: hidden;
		position: relative;
		width: 270%;

		@media (min-width: 640px) {
			width: 135%;
		}
		@media (min-width: 820px) {
			display: flex;
			justify-content: space-between;
			width: 100%;
			align-items: flex-start;
		}
	}

	// .news-block__column
	&__column {
		width: 33%;
		float: left;
		@media (min-width: 800px) {
			flex-basis: 300px;
		}
	}

	// .news-block__item
	&__item {
		margin-bottom: 12px;
		padding-right: 20px;
	}

	// .news-block__item-date
	&__item-date {
		font-weight: 700;
		display: inline;
	}

	// .news-block__article
	&__article {
		display: inline;
	}
}

