@use "sass:math";

.icon {
    display: inline-block;
    width: 1em;
    height: 1em;
    fill: currentColor;
}

.icon-arrow-slider {
    width: math.div(45.279, 45.279) * 1em;
}
.icon-arrow-up {
    width: math.div(32, 32) * 1em;
}
.icon-back-arrow {
    width: math.div(337.79, 337.79) * 1em;
}
.icon-close {
    width: math.div(18, 18) * 1em;
}
.icon-facebook {
    width: math.div(10, 20) * 1em;
}
.icon-instagram {
    width: math.div(40, 41) * 1em;
}
.icon-longArrow {
    width: math.div(476.21, 476.21) * 1em;
}
.icon-plus18 {
    width: math.div(328.86, 328.86) * 1em;
}
.icon-search {
    width: math.div(50, 50) * 1em;
}
.icon-telegram {
    width: math.div(300, 300) * 1em;
}
.icon-tweet {
    width: math.div(21, 17) * 1em;
}
.icon-vk {
    width: math.div(24, 24) * 1em;
}
