.banner {
	// .banner__vertical-grid
	&__vertical-grid {
		// всё что ниже можно стереть после демонстрации + убрать слово "Реклама"
		display: flex;
		justify-content: center;
		align-items: center;
		background-color: $accent-light;
		height: calc(100% - 32px);
		// ==============================================
		margin-bottom: 32px;
	}

	// .banner__before-header
	&__before-header {
		// всё что ниже можно стереть после демонстрации + убрать слово "Реклама"
		display: flex;
		justify-content: center;
		align-items: center;
		background-color: $accent-light;
		height: 180px;
		@media (max-width: $md3+px) {
			height: 140px;
		}
		@media (max-width: $md4+px) {
			height: 90px;
		}
		// ==============================================
	}	

	// .banner__before-sliders
	&__before-sliders {
		// всё что ниже можно стереть после демонстрации + убрать слово "Реклама"
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		background-color: $accent-light;
		height: 320px;
		// ==============================================
		margin-bottom: 48px;
	}

	// .banner__before-footer
	&__before-footer {
		// всё что ниже можно стереть после демонстрации + убрать слово "Реклама"
		display: flex;
		justify-content: center;
		align-items: center;
		background-color: $accent-light;
		height: 240px;
		@media (max-width: $md4+px) {
			height: 480px;
		}
		// ==============================================
		z-index: 3;
		position: relative;
	}

	// .banner__post-news_after-title
	&__post-news_after-title {
		// всё что ниже можно стереть после демонстрации + убрать слово "Реклама"
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		background-color: $accent-light;
		height: 60px;
		// ==============================================
	}

	// .banner__partners-links
	&__partners-links{
		// всё что ниже можно стереть после демонстрации + убрать слово "Реклама"
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		background-color: $accent-light;
		height: 60px;
		// ==============================================
	}
}

.big-container .banner__main-block{
	// всё что ниже можно стереть после демонстрации + убрать слово "Реклама"
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: $accent-light;
	// ==============================================

	display: none;
	margin-top: 24px;
	@media (min-width: 1465px) {
		display: block;
		// размеры можно стереть
		width: 160px;
		height: 600px;
		// =====================
	}
	@media (min-width: 1730px) {
		// размеры можно стереть
		width: 300px;
		// =====================
	}

	&_left-side {
	}
	&_right-side {
	}
}

