.indieNews_popup{
display: none;
&._active{
	display: block;
}
}

.search-overlay{
	background: rgba(0,0,0,.6);
	bottom: 0;
	left: 0;
	position: fixed;
	right: 0;
	transition: background .2s ease-in-out;
	z-index: 1000;

	// .search-overlay__input
	&__input {
		margin: 4px;
		border: 1px solid #8a99a6;
		border-radius: 4px;
		box-shadow: inset 0 1px 5px rgb(0 0 0 / 10%);
		color: #292929;
		font-family: $font-family-base;
		font-size: 1.6rem;
		font-style: italic;
		line-height: 1.4;
		padding: 8px 16px;
		transition: border-color .2s;		
		width: calc(100% - 8px);
		overflow-y: auto;
		&:focus{
			border: 4px solid orange;
		}
	}

	

	// .search-overlay__body
	&__body{
		height: calc(100% - 44px);
		margin: 0 auto;
		max-width: 1100px;
		overflow-y: auto;
		padding-bottom: 48px;
		padding-top: 56px;
	}
}
