// <ОБОЛОЧКА>=======================
html {
	color: $dark;
	font-size: $fontSize;
}

.wrapper {
	width: 100%;
	min-height: 100%;
	overflow: hidden;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;

	&._loaded {
	}
}
//</ОБОЛОЧКА>=====================================================================
//<ОСНОВНОЙ БЛОК>====================================================================================================
body {
	font-size: 1.4rem;
	font-family: $font-family-base;
	color: $black;
	&._lock,
	&._lock2 {
		overflow: hidden;
	}
}

// new ================================================
.img-responsive {
	display: block;
	max-width: 100%;
	height: auto;
}

.d-none {
	margin-top: -15.24rem;
}

.wrapper .icon {
	position: absolute;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
}

.main {
	&__content {
		display: block;
		justify-self: stretch;
	}
	&___left-block {
	}
	&___right-block {
	}
}

// два баннера с боков
.small-container-two-banners .main{
	@media (min-width: 825px) {
		grid-template-columns: minmax(160px, 300px) 544px minmax(160px, 300px);
		display: grid;
		justify-content: center;
		align-items: start;
		margin: 0px auto;
		justify-items: center;
	}
}

// Один баннер справа
.small-container2 .main{
	@media (min-width: 950px) {
		grid-template-columns: minmax(50px, 300px) 720px minmax(160px, 300px);
		display: grid;
		justify-content: center;
		align-items: start;
		margin: 0px auto;
		justify-items: center;
	}
}

.big-container .main{
	@media (min-width: 1112px) {
		grid-template-columns: minmax(160px, 300px) 1112px minmax(160px, 300px);
		display: grid;
		justify-content: center;
		align-items: start;
		margin: 0px auto;
		justify-items: center;
	}
}

//</ОСНОВНОЙ БЛОК>====================================================================================================

//<ГОТОВЫЕ КЛАССЫ>====================================================================================================
.mb24 {
	margin-bottom: 24px;
	@media (max-width: $md3+px) {
		margin-bottom: 14px;
	}
}

.mbpt {
	margin-bottom: 24px;
	padding-top: 24px;
}

.btn {
	display: flex;
	justify-content: center;
	align-items: center;
	padding: 10px 20px;
	background: $accent;
	color: #fff;
	line-height: 1;
	transition: all 0.2s ease 0s;
	@media (max-width: $md2+px) {
		padding: 8px 17px;
	}
	@media (max-width: $md3+px) {
		padding: 5px 10px;
	}
	@media (max-width: 435px) {
		padding: 12px 15px;
	}
	@media (min-width: $md2+px) {
		&:hover {
			background-color: darken($accent, 10%);
		}
	}
}

.link {
	color: $dark;
	transition: all 0.2s ease 0s;

	@media (min-width: $md2+px) {
		&:hover {
			color: $accent;
		}
	}
}
//=======================================================================================================================

.row:after {
	display: block;
	content: "";
	clear: both;
}
//====================================================================================================
%noselect {
	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-khtml-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
}
//====================================================================================================


//====================================================================================================
._ibg {
	position: relative;
	img {
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		object-fit: cover;
	}
}
body.ie {
	._ibg {
		background-position: center;
		background-size: cover;
		background-repeat: no-repeat;
	}
	._ibg img {
		width: 0;
		height: 0;
		opacity: 0;
		visibility: hidden;
	}
}
//====================================================================================================

//====================================================================================================

//====================================================================================================
%graystyle {
	transition: all 0.3s ease 0s;
	filter: grayscale(1);
	-webkit-filter: grayscale(1);
	-moz-filter: grayscale(1);
	-o-filter: grayscale(1);
}
%graystyleoff {
	filter: grayscale(0);
	-webkit-filter: grayscale(0);
	-moz-filter: grayscale(0);
	-o-filter: grayscale(0);
}
//====================================================================================================

//</ГОТОВЫЕ КЛАССЫ>===================================================================================
