.post-news {
	// .post-news__content
	&__content {
	}

	&__media-block {
		margin-bottom: 20px;
		@media (max-width: $md3+px) {
			margin-bottom: 15px;
		}
		@media (max-width: $md4+px) {
			margin-bottom: 10px;
		}
	}

	// .post-news__title-block
	&__title-block {
	}

	// .post-news__date
	&__date {
		color: $dark-light;
		font-family: $font-family-base;
		font-size: 1.4rem;
		line-height: 1.4;
		text-transform: lowercase;
	}

	// .post-news__title
	&__title {
		margin: 16px 0 8px 0;
		display: block;
	}

	// .post-news__AD
	&__AD {
	}

	// .post-news__text
	&__text {
		p {
			margin-bottom: 24px;
			@media (max-width: $md3+px) {
				margin-bottom: 14px;
			}
		}
	}

	// .post-news__slider-wrapper
	&__slider-wrapper {
	}

	// .post-news__image-block
	&__image-block {
	}

	// .post-news__image-wrapper
	&__image-wrapper {
		padding-bottom: 120%;
	}

	// .post-news__image
	&__image {
	}

	// .post-news__subtitle
	&__subtitle {
		margin-bottom: 20px;
		@media (max-width: $md3+px) {
			margin-bottom: 14px;
		}
	}

	// .post-news__social-links
	&__social-links {
		display: flex;
		justify-content: space-between;
		align-items: center;
		max-width: 180px;
		margin: 0px auto;

		li {
			background-color: $dark-lighter;
			border-radius: 50%;
			flex-shrink: 0;
			height: 40px;
			width: 40px;
			display: flex;
			justify-content: center;
			align-items: center;
		}
	}

	// .post-news__social-link
	&__social-link {
		position: relative;
		width: 24px;
		height: 24px;
	}

	// .post-news__end-line
	&__end-line {
		border-top: 1px solid $accent;
		margin-bottom: 15px;
	}

	// .post-news__comments
	&__comments {
	}

	// .post-news__comments-vk
	&__comments-vk {
		max-height: 0;
		transition: all 0.3s ease 0s;

		&._active {
			max-height: 100%;
			background-color: lighten($dark-light, 35%);
			margin-top: 20px;
			padding: 20px 0;

			@media (max-width: $md3+px) {
				padding: 12px 0;
				margin-top: 10px;
			}
		}
	}

	// .post-news__social-subcribe
	&__social-subcribe {
		border: 1px solid $accent;
		padding: 24px;
		margin-bottom: 24px;
		@media (max-width: $md3+px) {
			padding: 16px;
		}
	}

	// .post-news__social-text
	&__social-text {
		display: block;
		margin-bottom: 26px;
		text-align: center;
		font-family: $font-family-base;
		font-size: 1.6rem;
		letter-spacing: 1px;
		font-weight: 700;
		line-height: 1.4;
		@media (max-width: $md4+px) {
			margin-bottom: 22px;
		}
	}

	// .post-news__social-link
	&__social-link {
		width: 26px;
		height: 22px;
		position: relative;
	}

	// .post-news__social-link-icon
	&__social-link-icon {
		color: $dark;
		transition: all 0.2s ease 0s;
		@media (min-width: $md2+px) {
			&:hover {
				color: $dark-light;
			}
		}
	}

	// .post-news__social-links-share
	&__social-links-share {
		margin-top: 24px;
	}
}

.links-share {
	position: relative;
	z-index: 2;
	width: 42px;
	display: flex;
	flex-direction: column;
	align-items: center;
	overflow: hidden;
	justify-content: flex-start;
	border: 1px solid $accent;
	border-radius: 10px;
	padding: 8px 0;
	max-height: 283px;
	transition: max-height, opacity 0.7s, 0.7s ease, ease-out 0.1s, 0s;

	@media (min-width: 885px) and (orientation: landscape) {
		opacity: 0;
		animation: showYourSelf 0.7s ease 0s forwards;
	}
	@media (max-width: 885px) {
		position: fixed;
		right: 30px;
	}
	@media (max-width: $md4+px) {
		bottom: 190px;
		right: 17px;
	}
	@keyframes showYourSelf {
		0% {
			opacity: 0;
		}
		100% {
			opacity: 1;
		}
	}

	&._active {
		max-height: 46px;
	}

	&._show-wide-screen {
		margin-top: -676px;
		position: fixed;
		transition: max-height 0.7s ease 0.1s;
		opacity: 0;
		animation: showYourSelf 0.4s ease 0s forwards;
	}

	// .links-share__button
	&__button {
		width: 24px;
		padding-bottom: 28px;
		position: relative;
		cursor: pointer;

		// .links-share__button_icon
		&_icon {
			color: $dark;
			width: 24px;
			height: 22px;
			transition: all 0.2s ease 0s;

			@media (min-width: $md2+px) {
				&:hover {
					color: $accent;
				}
			}
		}
	}

	// .links-share__list
	&__list {
		height: 100%;
	}

	// .links-share__item
	&__item {
		margin: 8px;
		border-radius: 50%;
		background-color: $dark;
		width: 26px;
		height: 26px;
		display: flex;
		align-items: center;
		justify-content: center;
	}

	// .links-share__link
	&__link {
		position: relative;
		width: 16px;
		height: 16px;
		// .links-share__link_icon
		&_icon {
			color: $white;
			@media (min-width: $md2+px) {
				&:hover {
					color: $accent;
				}
			}
		}
	}
}

.partners-links {
	margin-bottom: 50px;
	@media (max-width: $md3+px) {
		margin-bottom: 40px;
	}
	@media (max-width: $md4+px) {
		margin-bottom: 30px;
	}

	// .partners-links__title
	&__title {
		margin-bottom: 20px;
		@media (max-width: $md4+px) {
			margin-bottom: 10px;
		}
	}
	// .partners-links__AD-block
	&__AD-block {
	}
}

