.rubric-incident-title {
	// .rubric-incident-title__content
	&__content {
		padding: 0 32px;
		@media (max-width: $md4+px) {
			padding: 0 16px;
		}
	}

	// .rubric-incident-title__top-block
	&__top-block {
		margin: 50px 0 32px 0;
		@media (max-width: $md3+px) {
			margin: 40px 0 26px 0;
		}
		@media (max-width: $md4+px) {
			margin: 25px 0 18px 0;
		}
	}

	// .rubric-incident-title__title
	&__title {
		margin-bottom: 24px;
	}

	// .rubric-incident-title__subtitle
	&__subtitle {
	}

	// .rubric-incident-title__separator
	&__separator {
		margin-bottom: 50px;
		width: 100%;
		height: 4px;
		background-color: $accent;
		@media (max-width: $md3+px) {
			margin-bottom: 40px;
		}
		@media (max-width: $md4+px) {
			margin-bottom: 25px;
		}
	}
}

.rubric-incident {
}

.rubric-incident-button{
	margin-bottom: 50px;
	@media (max-width: $md3+px){
		margin-bottom: 35px;
	}
	@media (max-width: $md3+px){
		margin-bottom: 25px;
	}
}