.lenta {
	margin-bottom: 64px;
	@media (max-width: $md3+px){
		margin-bottom: 40px;
	}
	@media (max-width: $md4+px){
		margin-bottom: 30px;
	}

	// .lenta__content
	&__content {
	}

	// .lenta__top-block
	&__top-block {
		margin: 32px 16px;
		@media (max-width: $md4+px){
			margin: 26px 16px;
		}
	}

	// .lenta__title
	&__title {
		margin: 16px 0;
	}

	// .lenta__subtitle
	&__subtitle {
	}

	// .lenta__middle-text-block
	&__middle-text-block {
		padding: 0 16px;
	}

	// .lenta__bottom-block
	&__bottom-block {
		margin-top: 30px;
	}

	// .lenta__show-more
	&__show-more {
		margin: 0px auto;
		text-transform: uppercase;
		border: 2px solid $accent;
		display: flex;
		justify-content: center;
		align-items: center;
		padding: 14px 30px;
		color: $accent;
		line-height: 1;
		transition: all 0.2s ease 0s;
		font-family: $font-family-base;
		font-size: 1.2rem;
		font-weight: 700;
		letter-spacing: 1px;
		min-width: 150px;
		cursor: pointer;

		@media (min-width: $md2+px) {
			&:hover {
				background-color: $accent;
				color: $white;
			}
		}
	}
}

.text-lenta {
	// .text-lenta__date-block
	&__date-block {
		margin-top: 32px;
	}

	// .text-lenta__title-date
	&__title-date {
	}

	// .text-lenta__list
	&__list {
	}

	// .text-lenta__item
	&__item {
		margin-top: 16px;
	}

	// .text-lenta__item-top
	&__item-top {
	}

	// .text-lenta__time
	&__time {
		display: inline;
		font-size: 1.4rem;
		font-weight: 700;
		@media (max-width: $md4+px){
			font-size: 1.2rem;
		}
		
	}

	// .text-lenta__rubric
	&__rubric {
		display: inline;
		font-size: 1.4rem;
		@media (max-width: $md4+px){
			font-size: 1.2rem;
		}
	}

	// .text-lenta__text
	&__text {
		color: $dark;
		transition: all 0.2s ease 0s;
		font-family: $font-family-second;
		font-weight: 400;
		line-height: 1.4;
		font-size: 1.8rem;
		text-decoration: none;
		@media (min-width: $md2+px) {
			&:hover {
				color: $accent;
			}
		}
		@media (max-width: $md4+px){
			font-size: 1.4rem;
		}
	}
}
