.footer {
	background-color: $dark;
	z-index: 3;
	position: relative;

	// .footer_top
	&_top {
	}

	// .footer_bottom
	&_bottom {
		display: flex;
		align-items: center;
		justify-content: center;
		height: 110px;
	}

	// .footer__content
	&__content {
		padding-top: 40px;
		@media (max-width: $md3+px) {
			padding-top: 30px;
		}
		@media (max-width: $md4+px) {
			padding-top: 20px;
		}
	}
}

.top-footer {
	// .top-footer__text-copyright
	&__text-copyright {
		color: $white;
		text-align: center;
		margin-bottom: 20px;
		letter-spacing: 1px;
		p {
			margin-top: 5px;
			@media (max-width: $md3+px) {
				margin-top: 15px;
			}
		}
		a {
			font-size: 1.3rem;
			color: $accent;
		}
	}

	// .top-footer__row
	&__row {
		border-top: 1px solid $accent;
		padding-top: 10px;
		display: flex;
		@media (max-width: $md3+px) {
			display: block;
		}
	}

	// .top-footer__column
	&__column {
		flex: 0 1 50%;
	}

	// .top-footer__social
	&__social {
		@media (max-width: $md3+px) {
			margin-bottom: 25px;
		}
	}

	// .top-footer__social-text
	&__social-text {
		color: $white;
		margin-bottom: 20px;
		letter-spacing: 2px;
		@media (max-width: $md3+px) {
			text-align: center;
		}
	}

	// .top-footer__social-links
	&__social-links {
		display: flex;
		@media (max-width: $md3+px) {
			justify-content: space-around;
			max-width: 300px;
			margin: 0 auto;
		}
		li {
			margin-right: 30px;
			width: 26px;
			height: 22px;
			position: relative;

			@media (max-width: $md3+px) {
				margin: 0;
			}
		}
	}

	// .top-footer__social-link
	&__social-link {
	}

	// .top-footer__social-link-icon
	&__social-link-icon {
		color: $white;
		@media (min-width: $md2+px) {
			&:hover {
				transition: all 0.2s ease 0s;
				color: $accent;
			}
		}
	}

	// .top-footer__law-row
	&__law-row {
		display: flex;
		justify-content: space-between;
		align-items: center;
		@media (max-width: $md3+px) {
			display: block;
		}
	}

	// .top-footer__law-links
	&__law-links {
		display: flex;
		flex-direction: column;
		@media (max-width: $md3+px) {
			margin-bottom: 20px;
			text-align: center;
		}
	}

	// .top-footer__law-link
	&__law-link {
		letter-spacing: 2px;
		text-decoration: underline;
		margin-bottom: 15px;
		color: $white;
		&:last-child {
			margin: 0px 0px 0px 0px;
		}
		@media (min-width: $md2+px) {
			&:hover {
				transition: all 0.2s ease 0s;
				color: $accent;
			}
		}
		@media (max-width: $md3+px) {
			margin-bottom: 20px;
		}
	}

	// .top-footer__raiting-wrap
	&__raiting-wrap {
		margin-right: 15px;
		width: 32px;
		height: 32px;
		position: relative;
		text-align: right;
		@media (max-width: $md3+px) {
			margin: 0 auto;
		}
	}

	// .top-footer__raiting
	&__raiting {
	}

	// .top-footer__raiting-icon
	&__raiting-icon {
		color: pink;
	}
}

